@import '../../scss';

:host {
  display: block;

  ul {
    margin: 0;
    padding: 0;
  }

  phn-p-headline {
    font-size: p-px-to-rem(28px);
  }

  .model-link-content {
    @include hoverZoom();
    padding-bottom: 5px;
  }

  .skeleton-bodytype {
    height: p-px-to-rem(135px);
    width: p-px-to-rem(260px);
    margin: p-px-to-rem(24px) 0 0 0 ;
    background: $pds-theme-light-contrast-low;
    border-radius: 4px;

    @include pds-media-query-min('xs') {
      height: p-px-to-rem(110px);
    }

    @include pds-media-query-min('s') {
      height: p-px-to-rem(120px);
      width: p-px-to-rem(260px);
    }
  }

  &.skeleton {
    pointer-events: none;
    user-select: none;
    opacity: 60%;

    & .skeleton-headline {
      width: auto;
      height: p-px-to-rem(38px);
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;
    }

    & .skeleton-link {
      width: auto;
      height: p-px-to-rem(48px);
      margin: p-px-to-rem(16px) 0;
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;
    }

    & .skeleton-image {
      width: auto;
      height: p-px-to-rem(135px);
      margin: p-px-to-rem(24px) 0;
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;

      @include pds-media-query-min('xs') {
        height: p-px-to-rem(145px);
      }

      @include pds-media-query-min('s') {
        height: p-px-to-rem(120px);
      }

      @include pds-media-query-min('m') {
        height: p-px-to-rem(137px);
      }

      @include pds-media-query-min('l') {
        height: p-px-to-rem(133px);
      }

      @include pds-media-query-min('xxl') {
        height: p-px-to-rem(145px);
      }
    }
  }

  .links {
    .link {
      width: 100%;
      padding-top: p-px-to-rem(16px);
    }
  }

  .series-body-type {
    text-align: center;
    padding: p-px-to-rem(2px);
    margin-top: p-px-to-rem(2px);
  }

  .further {
    width: 100%;
    padding-top: p-px-to-rem(64px);
  }

  .car-models {
    phn-p-text {
      font-size: p-px-to-rem(16px);

      @include pds-media-query-min('xs') {
        font-size: p-px-to-rem(18px);
      }

      @include pds-media-query-min('s') {
        font-size: p-px-to-rem(16px);
      }

      @include pds-media-query-min('xxl') {
        font-size: p-px-to-rem(18px);
      }
    }
  }
}
