@import '../../scss';

@mixin shopSection(){
  .wishlist-container{
    padding: p-px-to-rem(16px) ;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    border-radius: p-px-to-rem(12px);
    margin-top: p-px-to-rem(48px);

    @include pds-media-query-max('s') {
      background-color: #EEEFF2;
    }

    .no-icon {
      padding-left: p-px-to-rem(28px);
    }
  }

}

:host {
  display: block;
  box-sizing: border-box;

  @include shopSection()

}
