import { Fragment, JSX, h } from '@stencil/core';
import { getVehiclePurchaseRoutingKey } from '../../../entities/routing-key';
import { TLink } from '../../../components';
import { EventActions } from '../../../utility/constants';
import MenuItemCountIndicator from './menu-item-count-indicator';

export default function MenuItemLink({
  content,
  count,
  showSkeleton
}: {
  content: TLink | null;
  count: number;
  showSkeleton: boolean;
}): JSX.Element | null {
  return content ? (
    <Fragment>
      <phn-menu-item
        elementId={getVehiclePurchaseRoutingKey(content.id)}
        key={content.id}
        class={`${showSkeleton ? 'skeleton' : 'vehiclepurchase-link'}`}
        analyticsId={content.id}
        text={content.text}
        link={content.link}
        datalayerEventAction={EventActions.NAVIGATION_LINK_CLICK}
        role="listitem"
      >
        <MenuItemCountIndicator count={count} />
      </phn-menu-item>
    </Fragment>
  ) : null;
}
