@import '../../scss';

:host {
  display: block;

  @include pds-media-query-min('s') {
    margin-top: p-px-to-rem(-28px);
  }

  @include pds-media-query-min('xl') {
    margin-top: p-px-to-rem(-36px);
  }

  ul {
    margin: 0;
    padding: 0;
  }

  .skeleton-headline {
    width: auto;
    height: p-px-to-rem(50px);
    background: $pds-theme-light-contrast-low;
    border-radius: 4px;
  }

  .series {
    justify-content: center;

    margin: 0 0 p-px-to-rem(8px) 0;

    @include pds-media-query-min('s') {
      margin: 0 0 p-px-to-rem(8px) 0;
    }

    @include pds-media-query-min('m') {
      margin: 0 0 p-px-to-rem(4px) 0;
    }

    @include pds-media-query-min('xl') {
      margin: 0 0 p-px-to-rem(8px)  0;
    }

    .image {
      @include hoverZoom();

      @include pds-media-query-min('base') {
        width: p-px-to-rem(174px);
      }

      @include pds-media-query-min('xs') {
        width: p-px-to-rem(239px);
      }

      @include pds-media-query-min('s') {
        width: p-px-to-rem(174px);
      }

      @include pds-media-query-min('l') {
        width: p-px-to-rem(196px);
      }

      @include pds-media-query-min('xxl') {
        width: p-px-to-rem(239px);
      }
    }

    &.skeleton {
      margin: 0 0 p-px-to-rem(8px) 0;

      @include pds-media-query-min('xs') {
        margin: 0 0 p-px-to-rem(16px);
      }

      @include pds-media-query-min('xxl') {
        margin: 0 0 p-px-to-rem(24px) 0;
      }
    }
  }
}
