import { Component, Element, Event, EventEmitter, h, Host, JSX, State } from '@stencil/core';
import { componentsOnReady } from '../../utility/ready';
import { Timeout } from '../../utility/timeout';
import Links from './myporsche-drawer-links';
import Login from './myporsche-drawer-login';
import { navContentStore } from '../../state/nav-content-store';

@Component({
  tag: 'phn-myporsche-drawer',
  styleUrl: 'myporsche-drawer.scss',
  scoped: true
})
export class MyPorscheDrawer {
  @State() showSkeleton = true;

  @Element() el!: HTMLPhnMyporscheDrawerElement;

  @Event() logoutEvent!: EventEmitter<void>;

  private async waitForComponentsToLoad() {
    await componentsOnReady(this.el, new Timeout(5000));
    this.showSkeleton = false;
  }

  componentWillLoad(): void {
    this.waitForComponentsToLoad();
  }

  render(): JSX.Element | null {
    const { myPorsche } = navContentStore.state;

    if (!myPorsche) return null;

    return (
      <Host class={this.showSkeleton ? 'skeleton' : ''}>
        <div class="phn-flex-col phn-justify-around">
          <Login content={myPorsche} showSkeleton={this.showSkeleton} />
          <Links content={myPorsche} onLogoutClick={() => this.logoutEvent.emit()} showSkeleton={this.showSkeleton} />
        </div>
      </Host>
    );
  }
}
