@import '../../scss';

:host {
  display: block;

  .skeleton {
    width: auto;
    height: p-px-to-rem(24px);
    background: $pds-theme-light-contrast-low;
    border-radius: 4px;
  }

  .skeleton-headline {
    width: auto;
    height: p-px-to-rem(50px);
    background: $pds-theme-light-contrast-low;
    border-radius: 4px;
  }

  ul {
    padding: 0;
    margin: 0;
  }

  phn-headline {
    margin-bottom: p-px-to-rem(36px);
  }

  phn-menu-item {
    margin-top: p-px-to-rem(24px);

    @include pds-media-query-min('xs') {
      margin-top: p-px-to-rem(20px);

      &:first-of-type {
        margin: 0;
      }
    }

    @include pds-media-query-min('l') {
      margin-top: p-px-to-rem(32px);
    }

    .vehiclepurchase-link {
      margin-top: p-px-to-rem(24px);

      @include pds-media-query-min('xs') {
        margin-top: p-px-to-rem(20px);
      }

      @include pds-media-query-min('l') {
        margin-top: p-px-to-rem(30px);
      }

      .menu-item-count {
        margin-left: p-px-to-rem(16px);
      }
    }
  }
}
