import { breakpoint } from '@porsche-design-system/components-js/styles';
import { Component, Element, Fragment, getAssetPath, h, Host, Prop, State, Watch } from '@stencil/core';
import { BodyType, Series, TLink } from '../../entities/content';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';
import { navStateStore } from '../../state/nav-state-store';
import { ClickElementTypes, ElementTypes, EventActions } from '../../utility/constants';
import { getConfig, getHTMLElement, isString } from '../../utility/helper';
import { componentsOnReady } from '../../utility/ready';
import { Timeout } from '../../utility/timeout';
import analyticsManager from '../../services/analytics/analytics-manager';
import { getCurrentRoute } from '../../services/routing-manager';

@Component({
  tag: 'phn-car-body-types-drawer',
  styleUrl: 'car-body-types-drawer.scss',
  assetsDirs: ['model-assets'],
  scoped: true
})
export class CarBodyTypeDrawer {
  @Prop() series!: Series;
  @Prop() breakpoint!: number;

  @State() seriesLoaded = false;
  @State() showSkeleton = true;

  @Element() el!: HTMLPhnCarBodyTypesDrawerElement;

  @Watch('series')
  seriesWatcher(): void {
    this.seriesLoaded = false;
    this.showSkeleton = true;
  }

  private bodyTypesImagesLoaded = new Set<string>();
  private bodyTypesImagesNotLoaded = new Set<string>();

  private async waitForComponentsToLoad() {
    await componentsOnReady(this.el, new Timeout(5000));
    this.showSkeleton = !this.seriesLoaded;
  }

  componentWillLoad(): void {
    this.waitForComponentsToLoad();
  }

  componentDidRender(): void {
    this.showSkeleton = !this.seriesLoaded;
  }

  private handleImageLoad(seriesId: string) {
    this.bodyTypesImagesLoaded.add(seriesId);

    if (this.series.bodyTypes.every(({ id }) => this.bodyTypesImagesLoaded.has(id))) {
      this.seriesLoaded = true;
    }
  }

  private handleImageLoadError(seriesId: string) {
    this.handleImageLoad(seriesId);
    this.bodyTypesImagesNotLoaded.add(seriesId);
  }

  private sendMetaFunctionDataLayerEvent(link: TLink): void {
    globalDataLayerService.pushDataLayerEvent(EventActions.MODEL_CTA_CLICK, {
      componentClick: {
        clickElementId: link.id,
        clickElementName: link.text,
        clickElementType: ClickElementTypes.Link,
        targetUrl: link.link
      },
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType(
          getCurrentRoute(),
          ElementTypes.bodyTypeButton
        ),
        displayElementName: link.text
      },
      car: {
        modelRangeName: link.id.split('.')[1]
      }
    });
  }

  private sendBodyTypeClickDataLayerEvent(bodyType: BodyType, tagName: string): void {
    globalDataLayerService.pushDataLayerEvent(EventActions.MODEL_CLICK, {
      componentClick: {
        clickElementId: bodyType.id,
        clickElementName: bodyType.name,
        clickElementType: tagName === 'IMG' ? ClickElementTypes.Image : ClickElementTypes.Link,
        targetUrl: bodyType.link
      },
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType(
          getCurrentRoute(),
          ElementTypes.bodyTypeButton
        ),
        displayElementName: bodyType.name
      },
      car: {
        modelRangeName: bodyType.id.split('.')[1],
        modelName: analyticsManager.getModelName(bodyType)
      }
    });
  }

  private sendNavigationDataLayerEvent(): void {
    globalDataLayerService.pushDataLayerEvent(EventActions.MODEL_CLICK, {
      componentClick: {
        clickElementId: this.series.all.id,
        clickElementName: this.series.all.text,
        clickElementType: ClickElementTypes.Link,
        targetUrl: this.series.all.link
      },
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType(
          getCurrentRoute(),
          ElementTypes.bodyTypeButton
        ),
        displayElementName: this.series.all.text
      }
    });
  }

  private getImageSize(currentBreakpoint: number): number {
    switch (currentBreakpoint) {
      case breakpoint.xxl:
        return 1920;
      case breakpoint.xl:
      case breakpoint.l:
        return 1360;
      case breakpoint.m:
        return 1024;
      case breakpoint.s:
        return 760;
      case breakpoint.xs:
      case breakpoint.base:
      default:
        return 320;
    }
  }

  private getImageSource(image: string): string {
    const assetPath = getAssetPath(`${getConfig(navStateStore.state.env).ASSETS_URL}/model-assets`);
    const imageSize = this.getImageSize(this.breakpoint);
    const imagePath = `${assetPath}/${imageSize}`;

    return `${imagePath}/${image}.webp, ${imagePath}/${image}@2x.webp 2x`;
  }

  render(): HTMLPhnCarBodyTypesDrawerElement {
    return (
      <Host class={`${this.showSkeleton ? 'skeleton' : ''}`}>
        <Fragment>
          <ul class="links">
            {this.series.links.map((link) => (
              <Fragment>
                {this.showSkeleton ? <div class="skeleton-link" /> : null}
                <phn-p-link
                  key={link.id}
                  variant="tertiary"
                  href={link.link}
                  icon={link.icon}
                  size="inherit"
                  class="link"
                  style={{ display: this.showSkeleton ? 'none' : '' }}
                  onClick={() => {
                    this.sendMetaFunctionDataLayerEvent(link);
                  }}
                  role="listitem"
                >
                  {link.text}
                </phn-p-link>
              </Fragment>
            ))}
          </ul>
          <ul>
            {this.series.bodyTypes.map((bodyType) => {
              return (
                <Fragment>
                  {this.showSkeleton ? (
                    <Fragment>
                      <div class="skeleton-image" />
                    </Fragment>
                  ) : null}
                  <div
                    class="series-body-type"
                    key={bodyType.id}
                    style={{ display: this.showSkeleton ? 'none' : '' }}
                    role="listitem"
                  >
                    <phn-p-link-pure
                      icon="none"
                      onClick={(e: MouseEvent) => {
                        const clickedElement = getHTMLElement(e.target);
                        if (clickedElement !== null) {
                          this.sendBodyTypeClickDataLayerEvent(bodyType, clickedElement.tagName);
                        }
                      }}
                    >
                      <a href={bodyType.link}>
                        {this.bodyTypesImagesNotLoaded.has(bodyType.id) ? (
                          <Fragment>
                            <div class="skeleton-bodytype" />
                          </Fragment>
                        ) : (
                          <div class="model-link-content">
                            <img
                              srcSet={this.getImageSource(bodyType.image)}
                              alt={`${bodyType.name} Sideshot`}
                              onLoad={() => this.handleImageLoad(bodyType.id)}
                              onError={() => this.handleImageLoadError(bodyType.id)}
                            />

                            <phn-p-text size="inherit" color="inherit" align="center">
                              {bodyType.name}
                            </phn-p-text>

                            {isString(bodyType.description) ? (
                              <phn-p-text className="model-description" size="x-small" align="center">
                                {bodyType.description}
                              </phn-p-text>
                            ) : null}
                          </div>
                        )}
                      </a>
                    </phn-p-link-pure>
                  </div>
                </Fragment>
              );
            })}
          </ul>
          {this.showSkeleton ? null : (
            <phn-p-link
              variant="tertiary"
              href={this.series.all.link}
              size="inherit"
              class="further"
              data-test-id="car-body-types-drawer-further-links-button"
              onClick={() => {
                this.sendNavigationDataLayerEvent();
              }}
            >
              {this.series.all.text}
            </phn-p-link>
          )}
        </Fragment>
      </Host>
    );
  }
}
