import { getMenuContent } from '../../../services/menu-items-service';
import { navContentStore } from '../../../state/nav-content-store';
import { EventEmitter, Fragment, h } from '@stencil/core';
import { constructRoutingKey, RoutingKeys } from '../../../entities/routing-key';
import { isDesktop, IsLoggedIn, isString } from '../../../utility/helper';
import { EventActions } from '../../../utility/constants';
import { TShopAuthStatus, TShopMenu } from '../../../entities/content';
import { navStateStore } from '../../../state/nav-state-store';
import {
  filterWishlistItemsByAuthStatus,
  getAvailableShopVariables,
  getIconNameByMenuItemId
} from '../../../utility/helpers/shop-helper';
import { replaceVariables } from '../../../utility/helpers/string-helper';

type ShopSectionTypes = {
  activeRoutingKey: string;
  breakpoint: number;
  receivedContent: boolean;
  loginEvent: EventEmitter<void>;
};

export default function ShopSection({
  activeRoutingKey,
  breakpoint,
  receivedContent,
  loginEvent
}: ShopSectionTypes): HTMLElement {
  const showHeadline = !isDesktop(breakpoint);
  const displayedContent = getMenuContent(navContentStore.state, activeRoutingKey, RoutingKeys.SHOP) as TShopMenu;

  if (displayedContent === null) {
    return <div></div>;
  }

  const wishListAuthStatusList: TShopAuthStatus[] = IsLoggedIn(navStateStore.state.loggedInState)
    ? ['loggedIn', null]
    : ['loggedOut', null];

  const wishListItems = filterWishlistItemsByAuthStatus(displayedContent.additionalContent, wishListAuthStatusList);

  function emitLogin(id: string) {
    if (id === 'login') loginEvent.emit();
  }

  return (
    <Fragment>
      <phn-menu item={displayedContent} showHeadline={showHeadline} receivedContent={receivedContent} />
      {activeRoutingKey === RoutingKeys.SHOP ? (
        <div class="wishlist-container" role="list">
          {wishListItems.map((childItem) => {
            return (
              <phn-menu-item
                key={childItem.id}
                data-id={childItem.id}
                size={'x-small'}
                class={!(isString(childItem.iconUrl) || isString(childItem.icon)) ? 'no-icon' : ''}
                iconLeft={
                  (isString(childItem.id) && getIconNameByMenuItemId(childItem.id)) ||
                  childItem.icon ||
                  childItem.iconUrl
                }
                elementId={constructRoutingKey(activeRoutingKey, childItem.id)}
                analyticsId={childItem.id}
                text={replaceVariables(childItem.text, getAvailableShopVariables())}
                link={childItem.link}
                datalayerEventAction={
                  isString(childItem.link) ? EventActions.NAVIGATION_LINK_CLICK : EventActions.NAVIGATION_CLICK
                }
                onClick={() => {
                  emitLogin(childItem.id);
                }}
                role="listitem"
              />
            );
          })}
        </div>
      ) : null}
    </Fragment>
  );
}
