import { Component, Element, Event, EventEmitter, h, Host, JSX, Prop, State } from '@stencil/core';
import { TLink, TMenu, TVehiclePurchaseMenu } from '../../entities/content';
import { constructRoutingKey } from '../../entities/routing-key';
import { EventActions } from '../../utility/constants';
import { isString } from '../../utility/helper';
import { componentsOnReady } from '../../utility/ready';
import { Timeout } from '../../utility/timeout';
import { finderStateStore } from '../../state/finder-state-store';
import MenuItemLink from './saved-vehicles/menu-item-link';
import { addRoute, getCurrentRoute } from '../../services/routing-manager';

@Component({
  tag: 'phn-menu',
  styleUrl: 'menu.scss',
  scoped: true
})
export class Menu {
  @Prop() item!: TMenu | TVehiclePurchaseMenu;
  @Prop() showHeadline = true;
  @Prop() receivedContent = false;

  @State() showSkeleton = true;

  @Element() el!: HTMLPhnMenuElement;

  @Event({ bubbles: true, cancelable: false }) mobileMenuItemClick!: EventEmitter<string[]>;

  componentWillLoad(): void {
    this.waitForComponentsToLoad();
  }

  private savedVehiclesPosition = 3;

  private async waitForComponentsToLoad() {
    await componentsOnReady(this.el, new Timeout(5000));
    this.showSkeleton = !this.receivedContent;
  }

  // TODO: fix this
  private showMenu(childItem: TMenu): void {
    if (!isString(childItem.link)) {
      addRoute([childItem.id]);
    }
  }

  private getSavedVehiclesContent(content: TMenu | TVehiclePurchaseMenu): TLink | null {
    const savedVehicles = (content as TVehiclePurchaseMenu).savedVehicles;
    if (savedVehicles !== undefined) {
      return savedVehicles;
    }
    return null;
  }

  render(): JSX.Element {
    const displayedItem = this.item.children.length === 1 ? this.item.children[0] : this.item;
    const savedVehicles = this.getSavedVehiclesContent(this.item);
    const { savedVehiclesCount } = finderStateStore.state;

    const menuItems = displayedItem.children.map((childItem) => (
      <phn-menu-item
        key={childItem.id}
        class={`${this.showSkeleton ? 'skeleton' : ''}`}
        elementId={constructRoutingKey(getCurrentRoute(), childItem.id)}
        iconRight={childItem.children.length > 0 && !this.showSkeleton ? 'arrow-head-right' : ''}
        analyticsId={childItem.id}
        text={childItem.text}
        link={childItem.link}
        datalayerEventAction={
          isString(childItem.link) ? EventActions.NAVIGATION_LINK_CLICK : EventActions.NAVIGATION_CLICK
        }
        onClick={() => this.showMenu(childItem)}
        role="listitem"
      />
    ));

    return (
      <Host role="list">
        <ul>
          {menuItems}

          {savedVehicles
            ? menuItems.splice(
                this.savedVehiclesPosition,
                0,
                <MenuItemLink content={savedVehicles} count={savedVehiclesCount} showSkeleton={this.showSkeleton} />
              )
            : null}
        </ul>
      </Host>
    );
  }
}
