@import '../../scss';

@mixin set-skeleton {
  &.skeleton {
    pointer-events: none;
    user-select: none;

    & .skeleton-headline {
      width: 100%;
      height: p-px-to-rem(40px);
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;
      font-size: p-px-to-rem(28px);
      line-height: p-px-to-rem(40px);
      margin-bottom: p-px-to-rem(8px);

      @include pds-media-query-min('s') {
        margin-bottom: 0;
        font-size: p-px-to-rem(30px);
        line-height: p-px-to-rem(40px);
      }

      @include pds-media-query-min('m') {
        font-size: p-px-to-rem(28px);
        line-height: p-px-to-rem(40px);
      }

      @include pds-media-query-min('l') {
        @include pds-heading-medium;
      }
    }

    & .skeleton-login-container {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      border-radius: 4px;

      @include pds-media-query-min('xs') {
        flex-direction: row;
      }

      @include pds-media-query-min('s') {
        flex-direction: column;
      }

      @include pds-media-query-min('xxl') {
        flex-direction: row;
      }

      .skeleton-login {
        height: p-px-to-rem(48px);
        width: 100%;
        margin: p-px-to-rem(36px) 0 p-px-to-rem(16px) 0;
        border-radius: 4px;
        background: $pds-theme-light-contrast-low;

        @include pds-media-query-min('xs') {
          margin: p-px-to-rem(36px) p-px-to-rem(8px) p-px-to-rem(16px) 0;
        }

        @include pds-media-query-min('s') {
          margin: 0 p-px-to-rem(16px) p-px-to-rem(16px) 0;
        }

        @include pds-media-query-min('xl') {
          margin: 0 p-px-to-rem(16px) p-px-to-rem(23px) 0;
        }
      }

      .skeleton-more {
        height: p-px-to-rem(48px);
        margin: 0 0 p-px-to-rem(16px) 0;
        width: 100%;
        background: $pds-theme-light-contrast-low;
        border-radius: 4px;

        @include pds-media-query-min('xs') {
          margin: p-px-to-rem(36px) 0 p-px-to-rem(16px) p-px-to-rem(8px);
        }

        @include pds-media-query-min('s') {
          margin: 0 0 p-px-to-rem(23px) 0;
        }

        @include pds-media-query-min('xl') {
          margin: 0 p-px-to-rem(16px) p-px-to-rem(23px) 0;
        }
      }
    }

    & .skeleton-signup-container {
      padding-bottom: p-px-to-rem(24px);
      border-radius: 4px;
    }

    & .skeleton-signup {
      color: transparent;
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;
    }

    & .skeleton-menu {
      width: 100%;
      height: p-px-to-rem(48px);
      border-radius: 4px;
    }

    & .skeleton-menu-item {
      width: 100%;
      height: p-px-to-rem(24px);
      background: $pds-theme-light-contrast-low;
      margin-top: p-px-to-rem(24px);
      border-radius: 4px;
    }

    & .skeleton-menu-item:first-of-type {
      margin-top: p-px-to-rem(12px);
    }
  }
}

:host {
  display: block;
  margin-top: 0;

  @include set-skeleton;

  .myporsche-links {
    display: flex;
    flex-direction: row;
    justify-content: center;

    .myporsche-links-container {
      &.logged-in > .myporsche-link-item:first-child > phn-menu-item {
        padding-top: 0;
      }

      .myporsche-link-item > phn-menu-item {
        padding: p-px-to-rem(10px) 0 p-px-to-rem(10px) 0;

        @include pds-media-query-min('xxl') {
          padding: p-px-to-rem(16px) 0 p-px-to-rem(16px) 0;
        }
      }
    }

    .menu-item-count {
      margin-left: p-px-to-rem(16px);
    }
  }

  .headline-container {
    border-bottom: 2px solid $pds-theme-dark-background-base;

    @include pds-media-query-min('xs') {
      padding-bottom: p-px-to-rem(2px);
      display: flex;
      align-items: center;
    }

    @include pds-media-query-min('s') {
      padding-bottom: p-px-to-rem(16px);
    }

    @include pds-media-query-min('xxl') {
      padding-bottom: p-px-to-rem(24px);
    }

    .myporsche-headline {
      font-size: p-px-to-rem(28px);
      line-height: p-px-to-rem(40px);
      padding-bottom: p-px-to-rem(8px);

      @include pds-media-query-min('s') {
        padding-bottom: 0;
        font-size: p-px-to-rem(30px);
        line-height: p-px-to-rem(40px);
      }

      @include pds-media-query-min('m') {
        font-size: p-px-to-rem(28px);
        line-height: p-px-to-rem(40px);
      }

      @include pds-media-query-min('l') {
        @include pds-heading-medium;
      }
    }

    .headline-divider {
      padding: 0 p-px-to-rem(18px) 0 p-px-to-rem(16px);
      height: p-px-to-rem(32px);
    }

    .user-container {
      padding-bottom: p-px-to-rem(17px);
      display: inline-flex;
      align-items: center;

      @include pds-media-query-min('xs') {
        padding-bottom: 0;
      }

      phn-p-icon {
        padding-right: p-px-to-rem(7px);
        width: p-px-to-rem(20px);
        height: p-px-to-rem(20px);
      }

      phn-p-text {
        font-size: p-px-to-rem(12px);

        @include pds-media-query-min('xs') {
          padding: 0;
        }
      }
    }
  }

  phn-p-divider {
    display: block;
    height: 100%;
    align-self: center;
    padding: 0 p-px-to-rem(119px);

    @include pds-media-query-min('xl') {
      padding: 0 p-px-to-rem(150px);
    }
  }

}
