import { h, JSX } from '@stencil/core';
import { MyPorsche, TLink } from '../../entities/content';
import { getMyPorscheRoutingKey } from '../../entities/routing-key';
import { EventActions } from '../../utility/constants';
import { IsLoggedIn } from '../../utility/helper';
import { finderStateStore } from '../../state/finder-state-store';
import { navStateStore } from '../../state/nav-state-store';
import { shopStateStore } from '../../state/shop-state-store';
import { isShopAvailable } from '../../utility/helpers/shop-helper';
import { navContentStore } from '../../state/nav-content-store';
import featureToggles from '../../services/feature-toggles';

export default function Links({
  content,
  onLogoutClick,
  showSkeleton
}: {
  content: MyPorsche;
  showSkeleton: boolean;
  onLogoutClick: () => void;
}): JSX.Element {
  const messagesPosition = 1,
    savedSearchesPosition = 2,
    savedVehiclesPosition = 3,
    wishlistPosition = 4;
  const { loggedInState, unreadMessagesCount, app, locale } = navStateStore.state;
  const isLoggedIn = IsLoggedIn(loggedInState);
  const { links, messages, savedSearches, savedVehicles } = content;
  const { savedSearchesCount, savedVehiclesCount } = finderStateStore.state;
  const { wishlistItemsNumber } = shopStateStore.state.shopInformation;
  const { shop } = navContentStore.state;

  const convertedLinks = links.map<JSX.Element>((link) => (
    <div class="myporsche-link-item" key={link.id}>
      <phn-menu-item
        elementId={getMyPorscheRoutingKey(link.id)}
        class={`${showSkeleton ? 'skeleton' : ''}`}
        analyticsId={link.id}
        text={link.text}
        link={link.link}
        datalayerEventAction={EventActions.NAVIGATION_LINK_CLICK}
      />
    </div>
  ));

  convertedLinks.splice(
    messagesPosition,
    0,
    <MenuItemCountLink content={messages} count={unreadMessagesCount} showSkeleton={showSkeleton} />
  );

  if (savedSearches && savedSearches.text !== '') {
    convertedLinks.splice(
      savedSearchesPosition,
      0,
      <MenuItemCountLink content={savedSearches} count={savedSearchesCount} showSkeleton={showSkeleton} />
    );
  }
  if (savedVehicles && savedVehicles.text !== '') {
    convertedLinks.splice(
      savedVehiclesPosition,
      0,
      <MenuItemCountLink content={savedVehicles} count={savedVehiclesCount} showSkeleton={showSkeleton} />
    );
  }

  if (isShopAvailable(locale, app) && shop?.wishlist && featureToggles.isShopWishlistToggleActive()) {
    convertedLinks.splice(
      wishlistPosition,
      0,
      <MenuItemCountLink content={shop.wishlist} count={wishlistItemsNumber} showSkeleton={showSkeleton} />
    );
  }

  if (isLoggedIn) {
    convertedLinks.push(<LogoutLink content={content} onLogoutClick={onLogoutClick} showSkeleton={showSkeleton} />);
  }
  return (
    <div class="myporsche-links">
      {showSkeleton ? (
        <div class="skeleton-menu">
          {convertedLinks.map(() => (
            <div class="skeleton-menu-item" />
          ))}
        </div>
      ) : null}
      <div style={{ display: showSkeleton ? 'none' : '', width: '100%' }}>
        <div class={`${isLoggedIn ? 'logged-in' : ''} myporsche-links-container`}>{convertedLinks}</div>
      </div>
    </div>
  );
}

function MenuItemCountLink({
  content,
  count,
  showSkeleton
}: {
  content: TLink | null;
  count: number;
  showSkeleton: boolean;
}): JSX.Element | null {
  return content ? (
    <div class="myporsche-link-item" key={content.id}>
      <phn-menu-item
        elementId={getMyPorscheRoutingKey(content.id)}
        class={`${showSkeleton ? 'skeleton' : ''}`}
        analyticsId={content.id}
        text={content.text}
        link={content.link}
        datalayerEventAction={EventActions.NAVIGATION_LINK_CLICK}
      >
        <MenuItemCountIndicator count={count} />
      </phn-menu-item>
    </div>
  ) : null;
}

function LogoutLink({
  content,
  onLogoutClick,
  showSkeleton
}: {
  content: MyPorsche;
  showSkeleton: boolean;
  onLogoutClick: () => void;
}): JSX.Element {
  const { logout } = content;

  return (
    <div class="myporsche-link-item" key={logout.id}>
      <phn-menu-item
        elementId={getMyPorscheRoutingKey(logout.id)}
        class={`myporsche-menu-item logout${showSkeleton ? 'skeleton' : ''}`}
        analyticsId={logout.id}
        text={logout.text}
        onClick={onLogoutClick}
        datalayerEventAction={EventActions.NAVIGATION_LINK_CLICK}
      />
    </div>
  );
}

function MenuItemCountIndicator({ count }: { count: number }): JSX.Element | null {
  if (count <= 0) return null;

  return (
    <phn-p-tag slot="suffix" class="menu-item-count" color="notification-info-soft">
      {count}
    </phn-p-tag>
  );
}
